<template>
  <div class="unit_manage-page">
    <div class="unit_manage-main">
    <!-- <separator></separator> -->
      <i class="blue"></i>
      <span class="title">{{$t("unitManage.title")}}</span>
    <div class="main-content">
      <div class="unit-info">
        <el-form :model="unitform" class="demo-form-inline" label-width="130px">
          <p>
          <el-form-item :label="$t('unitManage.name')+'：'" >
            <el-input v-model="unitform.biography" maxlength="10"  :placeholder="$t('placeholder.plsCompanyName')" style="width:260px"></el-input>
          </el-form-item>
          </p>
          <p>
          <el-form-item :label="$t('unitManage.introduction')+'：'">
            <el-input
              type="textarea"
              :rows="2"
              maxlength="1000"
              :placeholder="$t('placeholder.plsCompanyDes')"
              :autosize="{ minRows: 8, maxRows: 4}"
              v-model="unitform.customerDescription"
              >
            </el-input>
          </el-form-item>
          </p>
          <p class="submit">
          <el-form-item class="buttonDiv">
            <el-button type="primary" @click="editUnitform" :disabled="!$route.meta['modify']">{{$t("unitManage.save")}}</el-button>
          </el-form-item>
          </p>
        </el-form>
      </div>
      <div class="img-list-box">
        <div class="img-info-label">{{$t("unitManage.upload")}}：</div>
        <div class="img-info-box">
        <el-row :gutter="12">
          <el-col :span="3" v-for="(o,index) in pictureList" :key="index" class="card">
            <el-card :body-style="{ padding: '0px' }">
              <el-carousel :autoplay="false" height="156px" indicator-position="none">
                <el-carousel-item v-for="(src,ind) in o.imgList" :key="ind" >
                  <img :src="netAPI.findClassificationPictureById+'?id='+src" class="image">
                </el-carousel-item>
              </el-carousel>
              <div class="card-content">
                <p class="omit">{{o.pictureDescription}}</p>
                <div class="bottom clearfix">
                  <time class="time">{{o.pictureClassification}}</time>
                  <el-button type="text" class="button del-btn" style="font-size:20px" icon="el-icon-delete" @click="remove(o)" :disabled="!$route.meta['photoRemove']"></el-button>
                  <el-button type="text" class="button" style="font-size:20px" icon="el-icon-edit" @click="showEditDialog(o)" :disabled="!$route.meta['photoModify']"></el-button>
                </div>
              </div>
            </el-card>
          </el-col>
          <el-col :span="3"   v-if="$route.meta['photoAdd']">
            <div @click="createDialog = true">
            <el-card :body-style="{ padding: '0px'}" class="add-icon">
              <!-- <div style="width:147px;height:147px"> -->
                 <i class="el-icon-plus" style="color: #dedede;margin:auto"  ></i>
              <!-- </div> -->


            </el-card></div>
          <!--<span style="padding-top:25px;position:absolute;color:#F56C6C;font-size:12px">{{$t("unitManage.requirement")}}</span>-->
          </el-col>
        </el-row>
        </div>
      </div>
    <!-- </div> -->
      <!--修改-->
    <el-dialog
      :title="$t('unitManage.modify')"
      :visible.sync="editDialog"
      width="30%"
      :before-close="editDialogClose">
        <el-form ref="editExhibition" :model="editFrom" label-width="120px" :rules="rules">
          <el-form-item :label="$t('unitManage.className')" prop="pictureClassification">
            <el-input v-model="editFrom.pictureClassification"></el-input>
          </el-form-item>
          <el-form-item :label="$t('unitManage.description')">
            <el-input v-model="editFrom.pictureDescription"></el-input>
            <!--<span>{{editFrom.fileList.length}}</span>-->
          </el-form-item>
          <el-form-item :label="$t('unitManage.pictures')" prop="file">
            <el-upload
              accept="image/jpeg,image/jpg,image/bmp,image/png"
              action=""
              ref='editFile'
              list-type="picture-card"
              :file-list="editFrom.fileList"
              :on-preview="handlePictureCardPreview"
              :auto-upload="false"
              :http-request="editExhibition"
              :on-change="editFilesChange"
              :on-remove="filesRemove"
              :limit="6"
              >
              <i class="el-icon-plus"></i>
              <div slot="tip" class="el-upload__tip">{{$t("unitManage.requirement")}}</div>
            </el-upload>
            <!--<el-upload-->
              <!--accept="image/jpeg,image/jpg,image/bmp,image/png"-->
              <!--action="https://jsonplaceholder.typicode.com/posts/"-->
              <!--:file-list="editFrom.fileList"-->
              <!--:auto-upload="false"-->
              <!--ref="editFile1"-->
              <!--list-type="picture-card"-->
              <!--:on-preview="handlePictureCardPreview"-->
              <!--:http-request="editExhibition1"-->
              <!--:on-remove="filesRemove"-->
              <!--:on-change="editFilesChange1"-->
              <!--:limit="6"-->
            <!--&gt;-->
              <!--<i class="el-icon-plus"></i>-->
              <!--<div slot="tip" class="el-upload__tip">{{$t("unitManage.requirement")}}</div>-->
            <!--</el-upload>-->
            <!--<el-upload-->
              <!--accept="image/jpeg,image/jpg,image/bmp,image/png"-->
              <!--action=""-->
              <!--ref='editFile'-->
              <!--list-type="picture-card"-->
              <!--:file-list="editFrom.fileList"-->
              <!--:on-preview="handlePictureCardPreview"-->
              <!--:auto-upload="false"-->
              <!--:http-request="editExhibition"-->
              <!--:on-change="editFilesChange"-->
              <!--:on-remove="filesRemove"-->
              <!--:limit="6"-->
            <!--&gt;-->
              <!--<i class="el-icon-plus"></i>-->
            <!--</el-upload>-->
          </el-form-item>
          <!--<el-form-item :label="$t('unitManage.panorama')" style="display:flex;align-items:center;">-->
            <!--<el-switch-->
              <!--style="display: block;margin-left: -52px;"-->
              <!--v-model="editFrom.vrPics"-->
              <!--active-color="rgba(67,131,220,1)"-->
              <!--inactive-color="#dedede"-->
              <!--:active-text="$t('common.enabled')"-->
              <!--:inactive-text="$t('common.disable')">-->
            <!--</el-switch>-->
          <!--</el-form-item>-->
        </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogClose"> {{$t("common.cancel")}}</el-button>
        <el-button type="primary" @click="submitForm('editExhibition')">{{$t("common.ok")}}</el-button>
      </span>
    </el-dialog>
    <el-dialog :visible.sync="imgdialogVisible">
      <img width="100%" :src="dialogImageUrl" alt="">
    </el-dialog>
      <!--展示-->
    <el-dialog
      :title="$t('unitManage.pictures')"
      :visible.sync="createDialog"
      width="30%"
      :before-close="createExhibitionClose">
        <el-form ref="createExhibition" :model="createFrom" :rules="rules" label-width="150px">
          <el-form-item :label="$t('unitManage.className')" prop="pictureClassification">
            <el-input v-model="createFrom.pictureClassification" ></el-input>
          </el-form-item>
          <el-form-item :label="$t('unitManage.description')">
            <el-input v-model="createFrom.pictureDescription"></el-input>
          </el-form-item>
          <el-form-item :label="$t('unitManage.pictures')" prop="file">
          <el-upload
            ref='createFile'
            action=""
            list-type="picture-card"
            :auto-upload="false"
            :on-preview="handlePictureCardPreview"
            :on-remove="createHandleRemove"
            :http-request="createExhibition"
            :on-change="filesChange"
            :limit="6"
            >
            <i class="el-icon-plus"></i>
            <div slot="tip" class="el-upload__tip">{{$t("unitManage.requirement")}}</div>
          </el-upload>
          </el-form-item>
          <!--<el-form-item :label="$t('unitManage.panorama')" style="display:flex;align-items:center;">-->
            <!--<el-switch-->
              <!--style="display: block;margin-left: -52px;"-->
              <!--v-model="createFrom.vrPics"-->
              <!--active-color="rgba(67,131,220,1)"-->
              <!--inactive-color="#dedede"-->
              <!--:active-text="$t('common.enabled')"-->
              <!--:inactive-text="$t('common.disable')">-->
            <!--</el-switch>-->
          <!--</el-form-item>-->
        </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="createExhibitionClose">{{$t("common.cancel")}}</el-button>
        <el-button type="primary" @click="submitForm('createExhibition')">{{$t("common.ok")}}</el-button>
      </span>
    </el-dialog>
    </div>
    </div>
  </div>
</template>
<script>
import separator from "../../../../public/separator.vue";
import APICONFIG from "../../../../../config/api.js";
export default {
  name: "unit_manage",
  components: {
    separator
  },
  data() {
    var self = this;
    var validatorCreateFile = (rule, value, callback) => {
      if (self.createFileNum == 0) {
        callback(new Error("请选择文件"));
      } else {
        callback();
      }
    };
    var validatoreditFile = (rule, value, callback) => {
      if (self.editFileNum == 0) {
        callback(new Error("请选择文件"));
      } else {
        callback();
      }
    };
    return {
      pictureList: [],
      unitform: {
        customerDescription: "",
        biography: ""
      },
      createFrom: {
        pictureClassification: "",
        pictureDescription: "",
        vrPics: false,
        files: []
      },
      phtotArr: [],
      createFileNum: 0, //创建图片个数
      editFileNum: 0, //修改图片个数
      editFrom: {
        id: "",
        pictureClassification: "", //分类
        pictureDescription: "", //描述
        vrPics: false, //vr开关
        files: [], //上传用户的
        fileList: [], //展示要用的
        readyDel: [] //准备删除的id列表
      },
      editDialog: false, //修改弹框状态
      createDialog: false, //创建弹框状态
      rules: {
        pictureClassification: [
          { required: true, message: "不能为空" },
          { min: 2, max: 12, message: "长度在 2 到 12 个字符" }
        ]
        // file: [{ validator: validatorCreateFile, message: "请上传图片" }]
      },
      imgdialogVisible: false,
      dialogImageUrl: ""
    };
  },
  created() {
    this.netAPI = APICONFIG().get("unit_manage");
    //单位信息
    this.unitformSubmit();
  },
  mounted() {
    this.getPictureList();
  },
  methods: {
    unitformSubmit() {
      this.$http.get(this.netAPI.findCustomerInfoById).then(
        res => {
          if (res.body.status == 200) {
            this.unitform.biography = res.body.data.biography;
            this.unitform.customerDescription =
              res.body.data.customerDescription;
          }
        },
        err => {
          this.$message({
            message: "服务错误" + err,
            type: "warning"
          });
        }
      );
    },
    editUnitform() {
      this.$http.post(this.netAPI.updateCustomerInfo, this.unitform).then(
        res => {
          if (res.body.status == 200) {
            this.$message({
              message: this.$t("common.updateSuccess"),
              type: "success"
            });
          }
        },
        err => {
          this.$message({
            message: "服务错误" + err,
            type: "warning"
          });
        }
      );
    },
    getPictureList() {
      this.$http.get(this.netAPI.findCustomerClassificationList).then(res => {
        if (res.body.status == 200) {
          this.pictureList = res.body.data;
          for (let item of this.pictureList) {
            this.$set(item, "imgList", "");
            this.addPicture(item);
          }
          console.log(this.pictureList)
        }
      });
    },
    addPicture(item) {
      this.$http
        .get(this.netAPI.findClassificationPictureIdList, {
          params: { ClassificationId: item.id }
        })
        .then(
          res => {
            if (res.body.status == 200) {
              item.imgList = res.body.data;
            }
          },
          err => {
            this.$message({
              message: "服务错误" + err,
              type: "warning"
            });
          }
        );
    },
    pictureUpload() {},
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          switch (formName) {
            case "createExhibition":
              this.$refs.createFile.submit();
              break;
            case "editExhibition":
              if (this.editFileNum > 0) {
              //   // debugger;
                this.$refs.editFile.submit();
              } else {
                this.editExhibition();
              }
              break;
            default:
              break;
          }
        } else {
          this.$message.error("请填写必填信息");
          return false;
        }
      });
    },
    filesChange(file, fileList) {
      //发生添加文件
      this.createFileNum++;
    },
    createHandleRemove() {
      this.createFileNum--;
    },
    editFilesChange() {
      this.editFileNum++;
    },
    filesRemove(file) {
      //file name 就是ID
      if (file.status == "success") {
        this.editFrom.readyDel.push(file.name);
      } else {
        this.editFileNum--;
      }
      console.log(this.editFrom.readyDel)
    },
    //第一次上传
    createExhibition(content) {
      console.log('上传')
      console.log(content.file)
      //倒计数
      if (this.createFileNum > 1) {
        this.createFrom.files.push(content.file);
        this.createFileNum--;
        return;
      }

      this.createFrom.files.push(content.file);
      let from = new FormData();
      from.append("pictureDescription", this.createFrom.pictureDescription);
      from.append(
        "pictureClassification",
        this.createFrom.pictureClassification
      );
      from.append("vrPics", this.createFrom.vrPics);
      for (let file of this.createFrom.files) {
        from.append("file", file);
      }
      console.log(from.get("file"))

      this.$http.post(this.netAPI.customerClassificationInsert, from).then(
        res => {
          if (res.body.status == 200) {
            this.getPictureList();
            this.createExhibitionClose();
          }
        },
        err => {
          this.$message({
            message: "服务错误" + err,
            type: "warning"
          });
        }
      );
    },
    //修改编辑
    editExhibition(content) {
      // 数据修改  发布数据不知道
      var kg = true;
      debugger;
      if (this.editFileNum > 0 && kg == true) {
        this.editFrom.files.push(content.file);
        this.editFileNum--;
        kg = false
        // return;
      }
      let textBody = {
        id: this.editFrom.id,
        pictureClassification: this.editFrom.pictureClassification,
        pictureDescription: this.editFrom.pictureDescription,
        vrPics: this.editFrom.vrPics
      };

      let readyDel = [];
      this.editFrom.readyDel.forEach((item, index) => {
        readyDel.push({ id: item });
      });
      console.log(readyDel)
        //修改文字OK
      let uptext = new Promise((resolve, reject) => {
        this.$http
            .post(this.netAPI.updateCustomerClassification, textBody)
            .then(
              res => {
                if (res.body.status == 200) {
                  resolve();
                } else {
                  reject();
                }
              }
            );
      });

        //删除数据OK
      let del = new Promise((resolve, reject) => {
        this.$http
            .post(this.netAPI.deleteClassificationPictureInBatch, readyDel)
            .then(
              res => {
                if (res.body.status == 200) {
                  resolve();
                } else {
                  reject();
                }
              }
            );
      });

      if(this.editFrom.files.length == 0) {
        Promise.all([uptext, del]).then(result => {
          this.editDialogClose();
          this.getPictureList();
          this.$message.success(this.$t("common.updateSuccess"));
        });
      }else {
        let form = new FormData();
        for (let file of this.editFrom.files) {
          form.append("file", file);
        }
        form.append("classificationId", this.editFrom.id);
        console.log(form.get("file"))
        let file = new Promise((resolve, reject) => {
          this.$http.post(this.netAPI.updateClassificationPicture, form).then(
            res => {
              if (res.body.status == 200) {
                resolve();
              } else {
                reject();
              }
            }
          );
        });

        Promise.all([uptext, del, file]).then(result => {
          this.editDialogClose();
          this.getPictureList();
          this.$message.success(this.$t("common.updateSuccess"));
        });
      }
      // try {
      //   // this.editFrom.files.push(content.file);
      //   console.log(this.editFrom.files)
      //   let form = new FormData();
      //   for (let file of this.editFrom.files) {
      //     form.append("file", file);
      //   }
      //   form.append("classificationId", this.editFrom.id);
      //
      //   let file = new Promise((resolve, reject) => {
      //     this.$http.post(this.netAPI.updateClassificationPicture, form).then(
      //         res => {
      //           if (res.body.status == 200) {
      //             resolve();
      //           } else {
      //             reject();
      //           }
      //         }
      //       );
      //   });
      //
      //   Promise.all([uptext, del, file]).then(result => {
      //     this.editDialogClose();
      //     this.getPictureList();
      //     this.$message.success("修改成功");
      //   });
      // } catch (error) {
      //   console.log(error)
      //   Promise.all([uptext, del]).then(result => {
      //     this.editDialogClose();
      //     this.getPictureList();
      //     this.$message.success("修改成功");
      //   });
      //   console.log("没有修改图片:" + error);
      // }
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      // debugger;
      this.imgdialogVisible = true;
    },
    handlePictureCardPreview1(file) {
      console.log(file)
    },
    editFilesChange1(file) {
      console.log(file)
    },
    createExhibitionClose() {
      //关闭并且清空
      // this.nulltoForm(this.createFrom, "createExhibition");
      this.createFrom = {
        pictureClassification: "",
        pictureDescription: "",
        vrPics: false,
        files: []
      };
      this.createDialog = false;
      this.createFileNum = 0; //创建图片个数
      this.$refs.createExhibition.resetFields();
      this.$refs.createFile.clearFiles();
    },
    showEditDialog(obj) {
      this.editDialog = true;
      this.editFrom.id = obj.id;
      this.editFrom.pictureClassification = obj.pictureClassification;
      this.editFrom.pictureDescription = obj.pictureDescription;
      this.editFrom.vrPics = obj.vrPics;
      this.editFrom.fileList = [];
      this.editFrom.files = [];
      this.editFrom.readyDel = [];
      obj.imgList.map(item => {
        this.editFrom.fileList.push({
          name: item,
          url: this.netAPI.findClassificationPictureById + "?id=" + item
        });
      })
    },
    editDialogClose() {
      //关闭并且清空
      this.editDialog = false;
      // this.nulltoForm(this.editFrom, "editExhibition");
      this.$refs.editFile.clearFiles();
    },
    nulltoForm(obj, formName) {
      for (let i in obj) {
        obj[i] = undefined;
      }
      this.$refs[formName].resetFields();
    },
    remove(obj) {
      this.$confirm("是否删除选中的相册？", "删除提示", {
        distinguishCancelAndClose: true,
        confirmButtonText: this.$t("common.delete"),
        cancelButtonText: this.$t("delete.cancel"),
        type: 'warning',
      }).then(
        () => {
          //先删除图片再删除相册
          this.beforeRemoveImgList(obj, _ => {
            //作为图片删除成功的回调
            _.$http
              .get(_.netAPI.deleteCustomerClassificationById, {
                params: { id: obj.id }
              })
              .then(
                res => {
                  if (res.data.status == 200) {
                    _.$message.success("相册删除成功！");
                    _.getPictureList();
                  }
                }
              );
          });
        }
      ).catch(() => {
        this.$message({
          type: "info",
          message: "已取消操作"
        });
      });
      // //先删除图片再删除相册
      // this.beforeRemoveImgList(obj, _ => {
      //   //作为图片删除成功的回调
      //   _.$http
      //     .get(_.netAPI.deleteCustomerClassificationById, {
      //       params: { id: obj.id }
      //     })
      //     .then(
      //       res => {
      //         if (res.data.status == 200) {
      //           _.$message.success("相册删除成功！");
      //           _.getPictureList();
      //         }
      //       }
      //     );
      // });
    },
    beforeRemoveImgList(obj, callback) {
      let ids = [];
      // debugger;
      obj.imgList.forEach((item, index) => {
        ids.push({ id: item });
      });

      //先删除图片
      this.$http.post(this.netAPI.deleteClassificationPictureInBatch, ids).then(
        res => {
          if (res.data.status == 200) {
            callback(this);
          }
        }
      );
    },
    pictureHandleRemove() {}
  }
};
</script>

<style lang="scss" scoped>
