}
</style>
<style lang="scss" scoped>
.unit_manage-page{
  width: 100%;
  height: 100%;
  position: relative;
  .unit_manage-main{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 30px;
    // margin: auto;
    background:rgba(255,255,255,1);
    border:1px solid rgba(233,233,233,1);
    box-shadow:#D1D1D1 0 2px 4px 0;
    border-radius:5px;
    box-sizing: border-box;
    padding: 20px;
    padding-top: 40px;
    .blue{
        width:12px;
        height:20px;
        background:rgba(67,131,220,1);
        border-radius:0px 3px 3px 0px;
        position: absolute;
        left: 0;
        top: 19px;
    }
    .title{
        position: absolute;
        left: 20px;
        top: 18px;
        font-size: 20px;
        font-weight: 600;
    }
    .main-content{
      width: 100%;
      // height: 100%;
      height: 770px;
      box-sizing: border-box;
      padding: 50px 240px;
      padding-bottom: 0;
      display: flex;
      flex-direction: column;
      @media screen and(max-width:1680px ){
        padding: 50px 20px;
      }
      .unit-info{
        height: 45%;
        .demo-form-inline{
          p{
            margin: 0;
          }
        }
      }
      .img-list-box{
        margin-left: -10px;
        // width: 100%;
        width: 1044px;
        height: 55%;
        display: flex;
        @media screen and(max-width: 1450px) {
          width: 100%;
        }
        .img-info-label{
          flex: 1.5;
        }
        .img-info-box{
          flex: 9;
          height: 100%;
          overflow-x: hidden;
          .el-row{
            display: flex;
            flex-wrap: wrap;
           >.card{
             width: 230px;
             height: 280px;
             margin: 0;
             margin-right: 6px;
             margin-top: 5px;
           }
          .add-icon {
            margin-top: 89px;
            margin-left: 7.48095%;
            text-align: center;
            width: 3.5%;
            cursor: pointer;
            transition: all 0.4s;
            width: 147px;
            height: 147px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            // margin-top: 100px;
            // margin-bottom: 40px;
            cursor: pointer;
            transition: all 0.4s;
            position: relative;
            &.add-icon:hover {
              box-shadow: 6px 8px 37px -2px;
            }
            .el-card{
              width: 100%;
              height:100%;
                i{
                // height: 67px;
                height: 100%;
                width: 100%;
                color: rgb(222, 222, 222);
                font-size: 28px;
                // line-height: 67px;
                display: flex;
                justify-content: center;
                align-items: center;
                // text-align: center;
              }
            }
          }

          }
        }
      }
    }
  }
}
</style>
<style>
.noShow .el-upload--picture-card {
  display: none !important;
}
.show .el-upload--picture-card {
  display: inline-block !important;
}
</style>




